* {
  box-sizing: border-box;
}
ul {
  padding: 0;
}
li {
  list-style: none;
}
body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country {
  align-items: center;
  display: flex;
}

.country-name {
  font-size: 35px;
  font-weight: bold;
  margin-left: 15px;
}
.country-name_list {
  margin-left: 10px;
  font-size: 20px;
}

.info_list {
  padding: 0;
}
.language {
  display: flex;
  align-items: baseline;
  padding: 0;
  padding-right: 5px;
  margin-top: 0;
}

.strong_card {
  font-size: 20px;
  padding-right: 5px;
}
.p {
  font-size: 20px;
  margin-bottom: 20px;
}

.country-list {
  list-style: none;
}
